import { IPlayer } from "../../../../types/player.type";
import Description from "../../../atoms/Description";
import { Wrapper } from "./styles";

export interface IProps {
  showingPlayer: IPlayer;
}

const CardPlayerInfo = ({ showingPlayer }: IProps) => {
  const getBirthDate = (birthDate: string) => {
    return birthDate.slice(0, 10);
  };

  const getDebutDate = (debutDate: string) => {
    return debutDate ? " " + debutDate.slice(0, 10) : "   ";
  };

  return (
    <Wrapper>
      <Description
        text={` : ${showingPlayer.FirstName} ${showingPlayer.LastName}`}
      />
      <Description text={` : ${showingPlayer.BirthCountry}`} />
      <Description
        text={` : ${getBirthDate(showingPlayer.BirthDate)}`}
      />
      <Description text={` : ${showingPlayer.Position}`} />
      <Description text={`  : ${showingPlayer.BatHand}`} />
      <Description text={`  : ${showingPlayer.ThrowHand}`} />
      <Description text={`  : ${getDebutDate(showingPlayer.ProDebut)}`} />
    </Wrapper>
  );
};

export default CardPlayerInfo;
